.app__page {
  display: flex;
}
.header {
	display:  flex;
	align-items: center;
	justify-content: space-between;
	padding: 20px;
	position: -webkit-sticky;
	position: sticky;
	top: 0;
	z-index: 100;
	background-color: white;
}

.header__logo {
	height:  25px;
	object-fit: contain;
	margin-left: 20px;
}

.header__input {
	display: flex;
	align-items: center;
	width: 40%;
	border: 1px solid lightgray;
}

.header__icon {
	margin-right: 8px;
	cursor: pointer;
}

.header__inputButton {
	width: 50px !important;
	background-color: #fafafa;
	color: gray;
}

.header__icons {
	display: flex;
	align-items: center;
}

.link {
	border-left: 1px solid lightgray;
}

.header__input > input {
	flex: 1 1;
	border: none;
	margin-left: 10px;
	outline: none;
}
.sidebar {
	flex: 0.2 1;
}

.sidebar > hr {
	height: 1px;
	border: 0;
	background-color: lightgray;
	margin-top: 10px;
	margin-bottom: 10px;
}
.sidebarRow {
	display: flex;
	align-items: center;
	padding: 10px 20px;
}

.sidebarRow__title {
	flex: 1 1;
	margin-left: 20px;
	font-size: 12px;
	font-weight: 500;
}

.sidebarRow__icon {
	color: gray;
}

.sidebarRow:hover {
	background-color: lightgray;
	cursor: pointer;
}

.sidebarRow:hover > .sidebarRow__icon {
	color: red;
}

.sidebarRow:hover > .sidebarRow__title {
	font-weight:  bold;
}

.sidebarRow.selected {
	background-color: lightgray;
}

.sidebarRow.selected > .sidebarRow__icon {
	color: red;
}

.sidebarRow.selected > .sidebarRow__title {
	font-weight: bold;
}
.recommendedVideos {
	flex: 0.8 1;
	background-color: #FDFEFE;
	padding: 10px 20px;
	padding-bottom: 0;
}

.recommendedVideos > h2 {
	margin: 0px 0px 20px 5px;
}

.recommendedVideos__videos {
	display: flex;
	flex-wrap: wrap;
}
.videoCard {
	cursor: pointer;
	margin-bottom: 20px;
	width: 270px;
}

.videoCard__thumbnail {
	height: 140px;
	width: 250px;
}

.videoCard__info {
	display: flex;
	margin-top: 4px;
	padding-right: 5px;
}

.videoCard__text {
	margin-left: 15px;
}

.videoCard__text > h4 {
	font-size:  14px;
	margin: 0px 0px 4px 0px;
}

.videoCard__text > p {
	font-size: 14px;
	color:  gray;
	margin: 0px;
}

.videoCard__avatar {
	height:  30px !important;
	width: 30px !important;
}
.watchVideoCard {
	flex: 0.65 1;
	margin: 20px 0px 0px 40px;
	background-color: #FDFEFE;

}

.watchVideoCard__video {
	width:  100%;
}

.watchVideoCard__info {
	padding-left: 2px;
}

.watchVideoCard__user {
	display: flex;
	align-items: center;
}

.watchVideoCard__buttonContainer {
	
}

.watchVideoCard__tipButton {
	border: none;
	border-radius: 2px;
	padding: 8px 16px;
	margin-left: 10px;
	background-color: #CC0000;
	color: #fff;
	font-weight: bold;
}

.watchVideoCard__user > p {
	margin-left: 10px;
	color: #030303;
}

.watchVideoCard__info > h3 {
	margin: 10px 0px 0px 0px;
	font-size: 18px;
	color: #030303;
}

.watchVideoCard__text > p {
	margin: 5px 0px 20px 0px;
	font-size: 14px;
	color:  #606060;
}

.watchVideoCard__info > hr {
	border: 1px solid lightgray;
}
.videoSideBar {
	flex: 0.3 1;
	background-color: #FDFEFE;
	margin: 25px 0px 0px 10px;
	padding-bottom: 0;
}
.sideBarVideo {
	display: flex;
	margin: 0px 0px 0px 0px;
	cursor: pointer;
}

.sideBarVideo__text {
	display: flex;
	flex-direction: column;
	margin-left: 8px;
}

.sideBarVideo > video {
	width: 160px;
	height: 100px;
}

.sideBarVideo__text > h3 {
	margin: 4px 0px 0px 0px;
	font-size: 14px;
	color: #030303;
}

.sideBarVideo__text > p {
	margin: 2px 0px 0px 0px;
	font-size: 12px;
	color: #606060;
}
.searchPage{
	flex: 0.8 1;
	background-color: #FDFEFE;
	padding: 10px 20px;
}

.searchPage__filter {
	display: flex;
	align-items: center;
	color: #606060;
	font-size:  xx-small !important;
}

.searchPage__filter > h2 {
	margin-left: 10px;
}

.searchPage > hr {
	height: 1px;
	border: 0;
	background-color: lightgray;
	margin-top: 10px;
	margin-bottom: 10px;
}
.channelRow {
	display:  flex;
	align-items: center;
	width: 70%;
}

.channelRow__logo {
	height: 100px !important;
	width: 100px !important;
	margin: 10px 40px;
}

.channelRow__text {
	display: flex;
	flex-direction: column;
}

.channelRow__icon {
	color: green;
}

.channelRow__text > p {
	color: #606060;
	font-size: small !important;
	margin: 2px 0px 0px 0px;
}

.channelRow__text > h4 {
	display: flex;
	align-items: center;
	justify-content: space-between;
	margin: 0px 0px 3px 0px;
}
.videoRow {
	display: flex;
	margin-bottom: 30px;
	max-width: 700px;
}

.videoRow > img {
	width: 246px;
	height: 138px;
}

.videoRow__text {
	margin-left: 14px;
}

.videoRow__description {
	margin-top: 20px;
	font-size: 12px;
	color: #606060;
}

.videoRow__subsNumber {
	color: blue;
}

.videoRow__headline {
	font-size: 12px;
	color: #606060;
}

.videoRow__subs {
	background-color: lightgray;
	padding:  2px;	
}

.videoRow__text > h3 {
	margin: 2px 0px 0px 0px;
}

.videoRow__text > p {
	margin: 5px 0px 0px 0px;
}
.upload__container {
	width:  50%;
	margin: 20px auto 20px auto;
	border: 1px solid lightgray;
	border-radius: 4px;
}

.upload__title {
	padding: 16px 16px 8px 16px;
	margin: 0px;
}

.upload__options {
	text-align: center;
	padding: 40px 0px 30px 0px;
}

.icon {
	margin-bottom: 20px;
}

.upload__button {
	font-size: 14px;
	background-color: #065FD4;
	color:  #fff;
	border-radius: 6px;
	padding: 10px 24px;
	cursor: pointer;
}

.upload__outputContainer {
	text-align: center;
	margin: 10px 0px;
}

.upload__output {
	max-width: 400px;
	border-radius: 8px;
}

.upload__titleInput {
	width: 75%;
	padding: 10px 10px;
	margin: 10px 0px;
	border: 1px solid lightgray;
	border-radius: 6px;
	outline: none;
}

.upload__submit {
	margin: 20px 0px 40px;
}

.upload__container > hr {
	border: none;
	height: 1px;
	background-color: lightgray;
}

.upload__options > h3 {
	font-size: 14px;
	margin: 0px 0px 2px 0px;
}

.upload__options > p {
	font-size: 12px;
	margin: 0px 0px 30px 0px;
}

input[type=file] {
	display: none;
}
body {
  margin: 0px;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

